*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars     
$mainColor: #E51937;

$md-1: 1200px;
$md-2: 1024px;
$md-3: 992px;
$md-4: 768px;
$md-5: 500px;

.body.lock {
    overflow: hidden;
}

.--svg__info {
	cursor: pointer;
}

.page {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	overflow: hidden;
	@media (max-width: $md-4) {
		display: none;
	}
	// .page__wrapper

	&__wrapper {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%;
	}

	// .page__screen

	&__screen {
	}

	// .page__pagination

	&__pagination {
		position: absolute;
		top: 50%;
		right: 20px;
		transform: translate(0px, -50%);
		display: flex;
		flex-direction: column;
		@media (max-width: $md-4) {
			display: none;
		}
	}

	// .page__scroll

	&__scroll {
		position: absolute;
		top: 0;
		right: 0;
		width: 4px;
		background-color: rgba(0, 0, 0, 0.5);
		height: 100%;
		background-color: rgba(0, 0, 0, 0.2);
	}

	&__bullet {
		width: 15px;
		height: 15px;
		cursor: pointer;
		background-color: rgba(0, 0, 0, 0.5);
		border-radius: 50%;
		margin: 10px 0;
		transition: .3s ease;
		// .page__bullet_active

		&_active {
			background-color: #000;
		}
	}

	&__drag-scroll {
		position: absolute;
		top: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.8);
		width: 100%;
		cursor: pointer;
	}
}

.content-without-scroll {
	display: none;
	@media (max-width: $md-4) {
		display: block;
	}
}

