.first-screen {
    height: 100vh;
    width: 100%;
    position: relative;
    padding: 10px 0 100px 0;
    // .first-screen__container

    &__container {
        max-width: 800px + 30px;
        height: 100%;
        margin: 0 auto;
        padding: 0 15px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }

    // .first-screen__header

    &__header {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 20px 0 0 0;
        @media (max-width: $md-2) {
            margin: 50px 0 0 0;
        }
        @media (max-width: $md-5) {
            margin: 40px 0 0 0;
        }
    }

    // .first-screen__title

    &__title {
        font-size: 90px;
        line-height: 110px;
        font-weight: 600;
        font-family: "Montserrat"; 
        @media (max-width: $md-5) {
            font-size: 50px;
            line-height: 70px;
        }
    }

    // .first-screen__subtitle

    &__subtitle {
        font-size: 15px;
        font-weight: 500;
        font-family: "Montserrat";
    }

    // .first-screen__bottom

    &__bottom {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
       @media (max-width: $md-5) {
           padding-bottom: 50px;
       }
    }

    // .first-screen__row

    &__row {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 0 15px 0;
        @media (max-width: $md-3) {
            flex-direction: column;
            margin: 0 0 30px 0;
        }
        @media (max-width: $md-5) {
            margin: 0 0 30px 0;
        }
    }


    // .first-screen__items

    &__items {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 375px;
        @media (max-width: $md-3) {
            margin:  0 0 50px 0;
            max-width: 100%;
        }
        @media (max-width: $md-2) {
            margin:  0 0 30px 0;
        }
        @media (max-width: $md-5) {
            margin:  0 0 25px 0;
        }
    }

    // .first-screen__item

    &__item {
        display: flex;
        align-items: center;
        @media (max-width: $md-3) {
            margin:  0 30px;
        }
        @media (max-width: $md-2) {
            margin:  0 15px;
        }
    }

    // .first-screen__ico

    &__ico {
        width: 55px;
        height: 50px;
        margin: 0 5px 0 0;
        @include bg('contain');
    }

    // .first-screen__column

    &__column {
        display: flex;
        flex-direction: column;
    }

    // .first-screen__caption

    &__caption {
        font-size: 35px;
        line-height: 24px;
        font-weight: 600;
        font-family: "Montserrat", sans-serif;
        margin: 0 0 5px 0;
        @media (max-width: $md-5) {
            font-size: 27px;
        }
    }
    &__caption span {
        font-size: 20px;
        @media (max-width: $md-5) {
            font-size: 15px; 
        }
    }

    // .first-screen__description

    &__description {
        font-size: 12px;
        font-family: "Montserrat", sans-serif;
        font-weight: 400;
        line-height: 15px;
    }

    // .first-screen__block

    &__block {
        display: flex;
        align-items: center;
        
    }

    // .first-screen__test

    &__test {
        margin: 0 40px 0 0;
        display: flex;
        align-items: center;
        @media (max-width: $md-5) {
            margin: 0 20px 0 0;
        }
    }

    // .first-screen__test-ico

    &__test-ico {
        width: 23px;
        height: 23px;
        @include bg("contain");
        @media (max-width: $md-4) {
            display: none;
        }
    }

    &__test-ico-red {
        width: 23px;
        height: 23px;
        @include bg("contain");
        display: none;
        @media (max-width: $md-4) {
            display: block;
        }
    }

    // .first-screen__test-text

    &__test-text {
        font-size: 13px;
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        line-height: 16px;
        margin: 0 0 0 7px 0;
        @media (max-width: $md-3) {
            color: $mainColor;
        }
    }

    // .first-screen__button

    &__button {
        @include no-btn();
        color: $mainColor;
        width: 160px;
        height: 40px;
        border: 2px solid $mainColor;
        font-size: 13px;
        font-weight: 700;
        font-family: "Montserrat", sans-serif;
        line-height: 16px;
        cursor: pointer;
        text-transform: uppercase;
        position: relative;
        &:hover {
            transition: .3s;
            color: #fff;
            &::before {
                transform: scale(1);
            }
        }
        @media (max-width: $md-3) {
            background-color: $mainColor;
            color: #fff;
        }
        
        &::before {
            position: absolute;
            content: "";
            background-color: $mainColor;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            transform: scale(0);
            transition: .3s;
            z-index: -1;
        }
    }

    // .first-screen__arrow-down

    &__arrow-down {
        display: none;
        width: 35px;
        height: 20px;
        cursor: pointer;
        transition: .5s;
        &:hover {
            transition: .5s;
            transform: translate(0, 4px);
        }
        @media (max-width: $md-4) {
            display: block;
        }
    }


    // .first-screen__background-image

    &__background-image {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: contain;
        top: -30px;
        left: 0;
        z-index: -50;
    }
}
