.description {
    width: 100%;
    height: 100vh;
    border-top: 1px solid $mainColor;
    @media (max-width: 650px) {
        border: none;
    }
    // .description__body

    &__body {
        display: flex;
        @media (max-width: $md-4) {
            flex-direction: column;
        }
    }

    // .description__left

    &__left {
        width: 100%;
        height: 100vh;
        position: relative;
        padding:  80px;
        @media (max-width: $md-3) {
            padding:  60px;
        }
        @media (max-width: $md-4) {
            height: 50vh;
            padding:  30px;
        }
    }

    // .description__title

    &__header  {
        @media (max-width: $md-4) {
            max-width: 320px;
            margin: 0 50px 0 0;
        }
        @media (max-width: 650px) {
            max-width: 100%;
            margin: 0;
        }
    }

    &__title {
        color: $mainColor;
        font-size: 70px;
        line-height: 85px;
        font-family: "Montsserat", sans-serif;
        font-weight: 700;
        @media (max-width: 650px) {
            display: none;
        }
    }

    // .description__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -50;
        width: 100%;
        height: 100%;
        object-fit: cover; 
    }

    // .description__right

    &__right {
        width: 500px;
        height: 100vh;
        background-color: $mainColor;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 60px 30px 70px 30px;
        @media (max-width: $md-4) {
            height: 50vh;
            width: 100%;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            padding: 30px 20px 50px 20px;
        }
       
    }

    // .description__caption

    &__caption {
        font-size: 30px;
        line-height: 37px;
        font-family: "Montsserat", sans-serif;
        font-weight: 600;
        margin: 0 0 20px 0;
        @media (max-width: 650px) {
            margin: 0 0 10px 0;
            font-size: 25px;
        }
    }

    // .description__text

    &__text {
        font-size: 15px;
        line-height: 19px;
        font-family: "Montsserat", sans-serif;
        font-weight: 500;
        @media (max-width: 650px) {
            font-size: 13px;
        }
    }

    // .description__column

    &__column {
        display: flex;
        flex-direction: column;
        height: 260px;
        justify-content: space-between;
        @media (max-width: 650px) {
            display: none;
        }
    }
    // .description__item

    &__item {
        display: flex;
        align-items: center;
    }

    // .description__ico

    &__ico {
        @include bg("contain");
        height: 50px;
        width: 40px;
        margin: 0 12px 0 0;
    }

    // .description__block

    &__block {
        display: flex;
        flex-direction: column;
    }

    // .description__name

    &__name-small {
        font-size: 20px;
        font-family: "Montsserat", sans-serif;
        font-weight: 600;
        line-height: 18px;
        max-width: 170px;
        margin: 0 0 3px 0;
    }

    &__name {
        font-size: 35px;
        font-family: "Montsserat", sans-serif;
        font-weight: 600;
        margin: 0 0 1px 0;
    }
    &__name span {
        font-size: 20px;
        font-family: "Montsserat", sans-serif;
        font-weight: 600;
    }

    // .description__info

    &__info {
        font-size: 12px;
        font-family: "Montsserat", sans-serif;
        font-weight: 400;
        line-height: 14px;
        max-width: 100px;
    }

    // .description__bottom

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 40px 0 0 0;
        @media (max-width: 650px) {
            display: none;
        }
    }

    // .description__more

    &__more {
        display: flex;
        align-items: center;
        margin: 0 30px 0 0;
    }

    &__more-ico {
        width: 23px;
        height: 23px;
        margin: 0 5px 0 0;
        @include bg("contain");
    }

    // .description__more-text

    &__more-text {
        text-transform: uppercase;
        font-size: 13px;
        font-family: "Montsserat", sans-serif;
        font-weight: 700;

    }

    // .description__button

    &__button {
        @include no-btn();
        color: #fff;
        width: 160px;
        height: 40px;
        text-transform: uppercase;
        font-size: 13px;
        line-height: 16px;
        border: 2px solid #fff;
        cursor: pointer;
        font-family: "Montsserat", sans-serif;
        font-weight: 700;
        z-index: 2;
        position: relative;
        &:hover {
            transition: .3s;
            color: $mainColor;
            &::before {
                transform: scale(1);
            }
        }
        &::before {
            position: absolute;
            content: "";
            background-color: #fff;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            transform: scale(0);
            transition: .3s;
            z-index: -1;
        }
    }
}
