.safe {
    width: 100%;
    height: 100vh;
    // .safe__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        @media (max-width: $md-4) {
            flex-direction: column-reverse;
        }
    }

    // .safe__left

    &__left {
        width: 500px;
        height: 100vh;
        background-color: $mainColor;
        color: #fff;
        padding: 60px 30px 70px 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @media (max-width: $md-4) {
            height: 50vh;
            width: 100%;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            padding: 30px 20px 50px 20px;
        }
    }

    // .safe__header

    &__header {
        @media (max-width: $md-4) {
            max-width: 320px;
            margin: 0 50px 0 0;
        }
        @media (max-width: 650px) {
            max-width: 100%;
            margin: 0;
        }
    }

    // .safe__title

    &__title {
        font-size: 30px;
        line-height: 37px;
        font-family: "Montsserat", sans-serif;
        font-weight: 600;
        margin: 0 0 20px 0;
        @media (max-width: 650px) {
            margin: 0 0 10px 0;
            font-size: 25px;
        }
    }

    // .safe__text

    &__text {
        font-size: 15px;
        line-height: 19px;
        font-family: "Montsserat", sans-serif;
        font-weight: 500;
        @media (max-width: 650px) {
            font-size: 13px;
        }
    }

    // .safe__content

    &__content {
        display: flex;
        flex-direction: column;
        margin: 20px 0 0 0;
        @media (max-width: 700px) {
            display: none;
        }
    }

    // .safe__caption

    &__caption {
        font-size: 30px;
        line-height: 36px;
        font-family: "Montsserat", sans-serif;
        font-weight: 600;
        margin: 0 0 30px 0;
    }

    // .safe__items

    &__items {
        display: flex;
        flex-direction: column;
    }

    // .safe__item

    &__item {
        display: flex;
        align-items: center;
        margin: 0 0 20px 0;
    }

    // .safe__name

    &__name {
        width: 130px;
        font-size: 17px;
        line-height: 21px;
        font-family: "Montsserat", sans-serif;
        font-weight:700;
        text-transform: uppercase;
        span {
            font-size: 10px;
            opacity: 0.7;
        }
    }
   
    // .safe__icos

    &__icos {
        display: flex;
        align-items: center;
    }

    // .safe__ico

    &__ico {
        width: 25px;
        height: 25px;
        @include bg("contain");
        margin: 0 2px;
    }

    // .safe__stars

    &__stars {
        display: flex;
        align-items: center;
        max-width: 280px;
        @media (max-width: 1000px) {
            display: none;
        }
    }

    // .safe__stars-ico

    &__stars-ico {
        width: 63px;
        height: 75px;
        margin: 0 20px 0 0;
        @include bg("contain");
    }

    &__stars-column {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    // .safe__stars-caption

    &__stars-caption {
        font-size: 50px;
        font-family: "Montsserat", sans-serif;
        font-weight:600;
    }

    // .safe__stars-text

    &__stars-text {
        font-size: 25px;
        font-family: "Montsserat", sans-serif;
        font-weight:600;
    }

    // .safe__bottom

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 40px 0 0 0;
        @media (max-width: 650px) {
            display: none;
        }
    }

    // .safe__more

    &__more {
        display: flex;
        align-items: center;
        margin: 0 30px 0 0;
    }

    // .safe__more-ico

    &__more-ico {
        width: 23px;
        height: 23px;
        margin: 0 5px 0 0;
        @include bg("contain");
    }

    // .safe__more-text

    &__more-text {
        text-transform: uppercase;
        font-size: 13px;
        font-family: "Montsserat", sans-serif;
        font-weight: 700;
    }

    // .safe__button

    &__button {
        @include no-btn();
        color: #fff;
        width: 160px;
        height: 40px;
        text-transform: uppercase;
        font-size: 13px;
        line-height: 16px;
        border: 2px solid #fff;
        cursor: pointer;
        font-family: "Montsserat", sans-serif;
        font-weight: 700;
        position: relative;
        z-index: 2;
        font-family: "Montsserat", sans-serif;
        &:hover {
            transition: .3s;
            color: $mainColor;
            &::before {
                transform: scale(1);
            }
        }
        &::before {
            position: absolute;
            content: "";
            background-color: #fff;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            transform: scale(0);
            transition: .3s;
            z-index: -1;
        }
    }

    // .safe__right

    &__right {
        width: 100%;
        height: 100vh;
        position: relative;
        @media (max-width: $md-4) {
            height: 50vh;
        }
    }

    // .safe__background

    &__background {
        z-index: -50;
        position: absolute;
        width: 90%;
        top: 97px;
        right: 0;
        @media (max-width: $md-4) {
            top: 20px;
            width: 600px;
        }
        @media (max-width: 650px) {
            width: 100%;
            right: 0;
            top: 60px;
        }
    }

    // .safe__image

    &__image {
        position: absolute;
        top: 18vh;
        left: -130px;
        z-index: 10;
        width: 1340px;
        height: 700px;
        @media (max-width: 1500px) {
            width: 1140px;
            height: 600px;
        }
        @media (max-width: 1400px) {
            width: 900px;
            left: -110px;
            object-fit: contain;
        }
        @media (max-width: $md-1) {
            width: 800px;
            left: -90px;
        }
        @media (max-width: 1100px) {
            width: 730px;
            top: 30vh;
            left: -80px;
        }
        @media (max-width: 1000px) {
            width: 600px;
            top: 50px;
            left: 0;
        }
        @media (max-width: 950px) {
            width: 500px;
            top: 30px;
        }
        @media (max-width: 850px) {
            width: 450px;
            top: 10px;
        }
        @media (max-width: 800px) {
            width: 400px;
            top: 0;
        }
        @media (max-width: $md-4) {
            width: 90%;
            top: -50px;
        }
        @media (max-width: 650px) {
            top: -70px;
        }
        @media (max-width: 500px) {
            top: -150px;
        }
    }
}
