


.header {
    width: 100%;
    height: 51px;
    position: relative; 
    // .header__container

    &__container {
        max-width: 1337px + 15px;
        height: 100%;
        padding: 0 15px;
        margin: 0 auto;
    }

    // .header__body

    &__body.active {
        &::before {
            content: "";
            z-index: 4;
            position: absolute;
            height: 100%;
            width: 100%;
            background-color: #fff;
            top: 0;
            left: 0;
        }
    }
    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
    }

    // .header__logo
    
    &__logo {
        width: 166px;
        height: 51px;
        z-index: 4;
    }

    // .header__row

    &__row {
        display: flex;
        align-items: center;
        z-index: 4;
    }

    // .header__item

    &__item {
        font-size: 15px;
        font-family: "Montserrat", sans-serif;
        font-weight: 600;
        line-height: 19px; 
        margin: 0 15px;
        cursor: pointer;
        @media (max-width: $md-3) {
            display: none;
        }
    }

    // .header__burger

    &__burger {
        margin: 0 0 0 15px;
        color: $mainColor;
        width: 24px;
        height: 15px;
        cursor: pointer;
        z-index: 4;
    }
}
.menu {
    @media (max-width: $md-3) {
        display: none;
    }
    z-index: 4;
   
    // .menu__items

    &__items {
        display: flex;
        align-items: center;
    }

    // .menu__item

    &__item {
        list-style: none;  
        position: relative; 
    }

    &__underline {
        width: 61px;
        height: 2px;
        position: absolute;
        top: 25px;
        left: 13px;
    }

    .item-active {
        color: $mainColor;
    }

    &__item a {
        text-decoration: none;
        color: #000;
        font-size: 15px;
        font-weight: 600;
        font-family: "Montserrat";
        margin: 0 15px;
        transition: .4s;
        cursor: pointer;
        &_active {
            color: $mainColor;
        }
        &:hover {
            transition: .4s;
            color: $mainColor;
            cursor: pointer;
        }
    }
}
.full-menu.visible {
    transition: .3s;
    left: 0;
}
.full-menu {
    transition: .3s;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-color: rgba(255, 255, 255, 0.959);
    color: #000;
    top: 0;
    left: -100%;
    padding: 121px 40px;
    @media (max-width: $md-4) {
        padding: 101px 10px;
    }
   

    &__row {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 0 60px 0;
        &_hidden {
            @media (max-width: $md-4) {
                display: none;
                margin: 0;
            }
        }
    }

    // .full-menu__items

    &__items {
        @media (max-width: $md-4) {
           width: 100%;
           padding: 0;
        }
        &_hidden {
            @media (max-width: $md-4) {
                display: none;
            }
        }
    }

    // .full-menu__item

    &__item {
        list-style: none;
        margin: 8px 0;
        @media (max-width: $md-4) {
            margin: 35px 0;
            display: flex;
            justify-content: center;
        }
        a {
            text-decoration: none;
            color: #000;
            cursor: pointer;
            font-size: 22px;
            font-family: "Montsserat", sans-serif;
            @media (max-width: $md-4) {
                font-size: 30px;
            }
            &:hover {
                color: $mainColor;
            }
        }
    }
}

